<template>
  <div>
    <v-chart class="chart" :option="option" theme="echartsTheme" />
  </div>
</template>

<script>
import VChart from "vue-echarts";
import china from "../../../static/PriceMap/china";
import "../../../theme/echartsTheme.js";

export default {
  components: {
    VChart,
  },
  data() {
    return {
      option: {
        tooltip: {
          // 
          // formatter https://echarts.baidu.com/option.html#tooltip.formatter
          formatter(params) {
            // params.data seriesdata
            let localValue, onlineValue;
            if (params.data) {
              localValue = params.data.value[0];
              onlineValue = params.data.value[1];
            } else {
              // 
              localValue = 0;
              onlineValue = 0;
            }
            let htmlStr = `
          <div style='font-size:18px;'> ${params.name}</div>
          <p style='text-align:left;'>
              ${localValue}<br/>
              ${onlineValue}<br/>
          </p>
        `;
            return htmlStr;
          },
          backgroundColor: "#ff7f50", //
          textStyle: { color: "#fff" }, //
        },
        visualMap: {
          show: true,
          bottom: 60,
          right: 100,
          text: [""],
          min: 195, //
          max: 205, //
          dimension: 0, //datavalue
          //   precision:0,
          itemHeight: 200,
          color: [
            // "#5475f5",
            "#9feaa5",
            "#85daef",
            "#74e2ca",
            // "#e6ac53",
            // "#9fb5ea",
          ],
        },
        // geo https://echarts.baidu.com/option.html#geo
        geo: {
          // 
          map: "china", // 
          // roam: true, // 
          zoom: 1.2, // 
          label: {
            // show: true
          },
          itemStyle: {
            //  
            //
            borderColor: "#979b99",
            areaColor: "#D8E9FD", //
            label: {
              // show: true, //
            },
          },
          emphasis: {
            itemStyle: {
              //
              // 
              shadowBlur: 20,
              shadowColor: "rgba(0, 0, 0, 0.5)",
              borderColor: "#fff",
              areaColor: "#D8E9FD",
            },
          },
        },
        series: [
          {
            name: "", // formatter
            type: "map",
            geoIndex: 0,
            label: {
              show: true,
            },
            // 
            data: [
              {
                name: "",
                value: [599, 300], //  
              },
            ],
          },
        ],
      },
    };
  },
  beforeMount() {
    this.generateData();
  },
  methods: {
    generateData: function () {
      let city = [
        "",
        "",
        "",
        "",
        "",
        "",
        "",
        "",
        "",
        "",
        "",
        "",
        "",
        "",
        "",
        "",
        "",
        "",
        "",
        "",
        "",
        "",
        "",
        "",
        "",
        "",
        "",
        "",
        "",
        "",
        "",
        "",
        "",
        "",
        "",
      ];
      let value1 = 200;
      let value2 = 180;
      let data = [];
      for (let i in city) {
        let info = {
          name: city[i],
          value: [value1 + Math.round(Math.random() * 10) - 5, value2],
        };
        data.push(info);
      }
      this.option.series[0].data = data;
    },
  },
};
</script>

<style scoped>
.chart {
  width: 1000px;
  height: 400px;
  margin: 20px auto 0 auto;
}
</style>


