<template>
  <div>
    <Top />
    <Breadcrumb :items="breadcrumb" />
    <SearchBar />
    <LeftOrRight :left="''" :right="''" @setLR="setDS" />
    <Classification
      @getTag="getTag"
      :name="clfName"
      :buttonName="clfButtonName"
      :items="clfItems"
      :url="clfUrl"
    />
    <DemandDisplay ref="dosd" />
    <Bottom />
  </div>
</template>

<script>
import Top from "../components/Shared/Top";
import Breadcrumb from "../components/Shared/Breadcrumb";
import SearchBar from "../components/Shared/SearchBar";
import LeftOrRight from "../components/Shared/LeftOrRight";
import Classification from "../components/Shared/Classification";
import DemandDisplay from "../components/DemandDisplay2B/DemandDisplay";
import Bottom from "../components/Shared/Bottom";

export default {
  components: {
    Top,
    Breadcrumb,
    SearchBar,
    LeftOrRight,
    Classification,
    DemandDisplay,
    Bottom,
  },
  data() {
    return {
      clfName: "",
      clfButtonName: "",
      clfItems: ["", "", "","","",""],
      clfUrl: "/demo",
      breadcrumb: [
        {
          name: "",
          url: { path: "/demanddisplay2b" },
        },
      ],
      tag: "",
    };
  },
  methods: {
    getTag: function (tag) {
      this.$refs.dosd.useTag(tag);
    },
    setDS: function (tag) {
      if (tag == "l") {
        this.clfName = "";
      } else {
        this.clfName = "";
      }
      this.$refs.dosd.setDS(tag);
    },
  },
};
</script>

<style>
</style>


